import { AfterViewInit, Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { BusinessProfile, FilterValue, SubscriptionInfo } from '@core/models';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { DestroyService } from '@shared/services/destroy.service';
import { Observable, takeUntil } from 'rxjs';
import { AgentStatus } from '@core/enums/agent.enum';
import { DEFAULT_EMAIL_PLACEHOLDER } from '@feature/my-profile/my-profile.constant';
import { MyProfileService } from '@core/services/my-profile.service';
import { APP_ROUTE } from '@core/constants';
import { DeleteBusinessDialogComponent } from '@feature/my-profile/components/delete-business-dialog/delete-business-dialog.component';
import { MatDialog, MatDialogModule } from '@angular/material/dialog';
import { SharedModule } from '@shared/shared.module';
import { MatDividerModule } from '@angular/material/divider';
import { SubscriptionModule } from '@feature/my-profile/v2/components/subscription/subscription.module';
import { BusinessInfoCardModule } from '@feature/my-profile/v2/components/business-info-card/business-info-card.module';
import { AuthService } from '@core/data-access/services/auth.service';
import { SubscriptionStatus, SubscriptionTier, SubscriptionType } from '@core/enums/profile.enum';
import { CancelSubscriptionDialogComponent } from '@feature/my-profile/v2/components/cancel-subscription-dialog/cancel-subscription-dialog.component';
import { SnackService } from '@core/services/snack.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'findqo-business-profile',
    templateUrl: './business-profile.page.html',
    styleUrl: './business-profile.page.scss',
    imports: [SharedModule, MatDialogModule, MatDividerModule, SubscriptionModule, BusinessInfoCardModule]
})
export class BusinessProfilePage implements OnInit, AfterViewInit {
	constructor(
		private myProfileFacade: MyProfileFacade,
		private destroy: DestroyService,
		private myProfileService: MyProfileService,
		private readonly dialog: MatDialog,
		private readonly auth: AuthService,
		public snackBarService: SnackService,
		private route: ActivatedRoute
	) {
		this.countyList$ = this.myProfileFacade.getCountyList$();
		this.countyAreaList$ = this.myProfileFacade.getCountyAreaList$();
		this.businessProfile$ = myProfileFacade.getBusinessProfile$();
		this.inviterName$ = myProfileService.inviterName$;
		this.subscription$ = this.myProfileFacade.getSubscriptionInfo$();
	}
	readonly buttonType = ButtonType;
	readonly agentStatus = AgentStatus;
	readonly appRoute = APP_ROUTE;
	readonly subscriptionStatus = SubscriptionStatus;
	readonly subscriptionTier = SubscriptionTier;
	readonly subscriptionType = SubscriptionType;

	hasBusinessAccount = false;
	selectedFile: File | null = null;
	isPhotoUploading = false;
	isChangeEmail = false;
	isExtendPaymentStrip = true;
	businessProfile$: Observable<BusinessProfile>;
	subscription$: Observable<SubscriptionInfo>;
	businessProfile: BusinessProfile;
	countyList$: Observable<FilterValue[]>;
	countyAreaList$: Observable<FilterValue[]>;
	defaultEmailPlaceholder = DEFAULT_EMAIL_PLACEHOLDER;
	isAgentUser = this.auth.user()?.roles?.some((role) => role === 'isAgentUser');

	inviterName$: Observable<string>;

	searchCounty = new FormControl('');
	searchCountyArea = new FormControl('');

	getUserDetails(): void {
		this.businessProfile$.pipe(takeUntil(this.destroy)).subscribe((business: BusinessProfile) => {
			if (business) {
				this.businessProfile = business;
				this.hasBusinessAccount = business?.name ? true : false;
			}
		});
	}

	openBusinessDeleteDialog(): void {
		this.dialog.open(DeleteBusinessDialogComponent, {
			width: '95%',
			maxWidth: '500px',
			height: 'auto',
		});
	}

	cancelSubscription(): void {
		this.dialog
			.open(CancelSubscriptionDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				width: '95%',
				data: this.myProfileFacade.getSubscriptionData(),
			})
			.afterClosed()
			.subscribe((res) => {
				if (res.value) {
					this.myProfileFacade.cancelSubscription(res.value).subscribe(() => {
						this.snackBarService.open({
							data: { message: 'Subscription cancelled', type: 'success' },
							panelClass: 'snackbar-success',
						});
					});
				}
			});
	}

	onClickWarningBanner(): void {
		document.getElementById('pricingList').scrollIntoView({ behavior: 'smooth', block: 'start' });
	}

	hideExtentPayment(): void {
		this.isExtendPaymentStrip = false;
	}

	ngAfterViewInit() {
		this.route.queryParams.pipe().subscribe((res) => {
			const params = res;
			if (params['pricing']) {
				setTimeout(() => {
					document.getElementById('pricing').scrollIntoView({ behavior: 'smooth', block: 'center' });
				}, 200);
			}
		});
	}
	ngOnInit(): void {
		this.myProfileFacade.loadBusinessProfile();
		this.getUserDetails();
	}
}
